.add-todo {
    background: var(--card-bg);
    border-radius: var(--card-border-radius);
    padding: 20px;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    &__input {
        border-radius: 5px;
        border: 1px solid #000000;
        padding: 8px 12px;
        font-size: 16px;
        line-height: 1.2;
    }
    &__textarea {
        border-radius: 5px;
        border: 1px solid #000000;
        padding: 8px 12px;
        font-size: 16px;
        line-height: 1.5;
        min-height: 100px;
        resize: none;
    }
    &__date {
        border-radius: 5px;
        border: 1px solid #000000;
        padding: 8px 12px;
        font-size: 16px;
        line-height: 1.2;
        width: 100%;
    }
    &__btn {
        background-color: var(--primary-color);
        color: #ffffff;
        padding: 8px 12px;
        font-size: 16px;
        line-height: 1.2;
        cursor: pointer;
        border-radius: 5px;
        border: 0;
    }
    label {
        margin-bottom: 5px;
        display: block;
    }
}